<template>
  <div id="page-user-list">
    <vs-card class="p-3 pb-1">
      <div class="flex flex-wrap">
        <div class="w-full sm:w-1/2 md:w-1/2 lg:w-1/2 xl:w-1/2">
          <h5 class="m-0 pt-3">Admins Account</h5>
        </div>
        <div class="w-full sm:w-1/2 md:w-1/2 lg:w-1/2 xl:w-1/2 text-right">
          <vs-button color="primary" to="/create-admin-account" type="filled">Create Admin Account</vs-button>
        </div>
      </div>
    </vs-card>
    <vs-card class="p-3 pb-1 vs-con-loading__container">
      <vs-table id="div-with-loading" search max-items="5" pagination :data="admins">
        <template slot="thead">
          <!-- <vs-th sort-key="email">Id</vs-th> -->
          <vs-th>Id</vs-th>
          <vs-th sort-key="createdAt">Date Created</vs-th>
          <vs-th sort-key="firstName">Full Name</vs-th>
          <vs-th>Email</vs-th>
          <vs-th>Phone</vs-th>
          <vs-th>Type</vs-th>
          <vs-th>Action</vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data">
            <vs-td :data="tr.id">{{ index + 1 }}</vs-td>
            <vs-td :data="tr.createdAt">{{ tr.createdAt | formatDate }}</vs-td>
            <vs-td :data="tr.firstName">{{ tr.firstName }} {{ tr.lastName }}</vs-td>
            <vs-td>{{tr.email}}</vs-td>
            <vs-td>{{tr.phoneNumber}}</vs-td>
            <vs-td :data="tr.type">{{ tr.type }}</vs-td>
            <vs-td>
              <vs-button
                :to="'/view-admin/'+ tr._id"
                color="primary"
                type="filled"
                size="small"
                class="mr-2 mb-2"
              >View</vs-button>
              <vs-button color="dark" size="small">Change Status</vs-button>
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>
    </vs-card>
  </div>
</template>

<script>
import moment from "moment";
import vSelect from "vue-select";
export default {
  components: {
    vSelect
  },
  data() {
    return {
      admins: []
    };
  },
  computed: {},
  methods: {
    openLoadingInDiv() {
      this.$vs.loading({
        container: "#div-with-loading",
        scale: 0.6
      });
    }
  },
  mounted() {
    moment().format();
    this.openLoadingInDiv();
    this.$store
      .dispatch("getAllAdmins")
      .then(resp => {
        this.admins = resp.data;
        console.log(this.admins);
        this.$vs.loading.close("#div-with-loading > .con-vs-loading");
      })
      .catch(err => {
        this.$vs.notify({
          title: "Load Admin",
          text: "Unable to Load list of Admin!",
          color: "danger"
        });
        this.$vs.loading.close("#div-with-loading > .con-vs-loading");
      });
  }
};
</script>

<style lang="scss">
#page-user-list {
  .user-list-filters {
    .vs__actions {
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-58%);
    }
  }
}
</style>
